@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
#root{
  margin: 0 auto;
  font-family: "poppins";

}

.header{
  text-align: center;
}

.header img{
  width: 150px;
  height: 200px;
  object-fit: contain;
}

.search-container input{
  padding: 15px;
  border-radius: 10px;
  border: none;
  width: 80%;
  margin: 20px;
  font-size: 18px;
 
}
.search-container button{
  width: 30%;
  margin-left: 30px;
  background-color:#027dfd;
}

.search-container{
  text-align: center;
}

.loading-container{
  margin-top: 20px;
  text-align: center;
}
.loading-container img{
  width: 50px;
}

@media(max-width:768px)
{
  h4{
    font-size: 24px;
  }
  h1{
    font-size: 45px;
  }
  .search-container input{
    font-size: 20px;
  }
  button{
    width: 40%;
    align-items: center;
  }


  .info-container{
   padding-left: 20px;
  }
}